/* src/styles.css */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .App {
    text-align: center;
    padding: 20px;
  }
  
  .home {
    margin: 0 auto;
    padding: 20px;
    max-width: 800px;
  }
  
  .quotes-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .quote {
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #f9f9f9;
  }
  
  @media (max-width: 600px) {
    .quote {
      padding: 15px;
    }
  
    .quotes-list {
      gap: 15px;
    }
  }
  