/* src/styles.css */
body {
  font-family: Arial, sans-serif;
  background-color: #ffffff;
  color: #333;
  margin: 0;
  padding: 0;
}

.navbar {
  background-color: #000000;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  margin: 0.5rem;
}

.category {
  padding: 2rem;
  text-align: center;
}

.category h1 {
  color: #0d0d0d;
}

.quote-card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 1rem auto;
  padding: 1.5rem;
  max-width: 600px;
  border-left: 5px solid #000000;
}

.quote-text {
  font-size: 1.2rem;
  color: #333;
}

.quote-author {
  font-size: 1rem;
  color: #777;
  text-align: right;
  margin-top: 1rem;
}

.home {
  text-align: center;
  padding: 5rem 2rem;
}

.home h1 {
  font-size: 2.5rem;
  color: #000000;
}

.home p {
  font-size: 1.2rem;
  color: #666;
}

/* Media Queries */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .quote-card {
    margin: 1rem;
    padding: 1rem;
  }

  .home h1 {
    font-size: 2rem;
  }

  .home p {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .navbar {
    padding: 0.5rem;
  }

  .navbar a {
    margin: 0.3rem;
  }

  .quote-card {
    padding: 0.8rem;
  }

  .quote-text {
    font-size: 1rem;
  }

  .quote-author {
    font-size: 0.9rem;
  }

  .home h1 {
    font-size: 1.5rem;
  }

  .home p {
    font-size: 0.9rem;
  }
}
